<template>
	<div class="box">
		<div class="bread">
			<el-breadcrumb separator-class="el-icon-arrow-right" class="breadItem">
			  <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="userDetail">
			<img src="" alt="" style="display: inline-block;width: 50px;height: 50px;
				vertical-align: middle;
			">
			<h3 style="display: inline-block;cursor: pointer;">
				{{ userName }} 
				<span 
				style="
					transform:rotate(90deg);
					display: inline-block;
				"
				>&#155;</span>
			</h3>
		</div>
		<div class="funBtn">
			<span>管理</span>
		    <el-divider direction="vertical"></el-divider>
		    <span>API文档</span>
		    <el-divider direction="vertical"></el-divider>
		    <span>帮助</span>
		</div>
	</div>
</template>

<script>
export default {
  data () {
    return {
    	userName:'用户'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.breadItem{
	display: inline-block;
	opacity: 0.6;
}
	.bread{
		display: inline-block;
		line-height: 60px;
		height: 60px;
	}
	.funBtn,.userDetail{
		display: inline-block;
		line-height: 60px;
		float: right;
		margin-right: 30px;
	}
	h3{
		margin:0;
	}
	.funBtn span{
		cursor: pointer;
	}
</style>



