<template>
  <div>
    <p>{{this.cardName.numName}}</p>
    <el-table
    :data="tableData"
    border
    style="width: 100%">
    <el-table-column
      prop="date"
      label="日期">
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名">
    </el-table-column>
   
    <el-table-column
      label="操作">
      <template slot-scope="scope">
        <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
        <el-button type="text" size="small">编辑</el-button>
      </template>
    </el-table-column>
  </el-table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      tableData: [{
          date: '2016-05-02',
          name: '王小虎',
         
        }, {
          date: '2016-05-04',
          name: '王小虎',
         
        }, {
          date: '2016-05-01',
          name: '王小虎',
         
        }, {
          date: '2016-05-03',
          name: '王小虎',
         
        }]
    }
  },
  props:['cardName'],
  methods:{
    handleClick(row) {
        console.log(row);
      },
      getData(){
        // axios.get/post(url,{data:data})
      }
  },
  watch:{
    'cardName':function(){
      console.log()
    }
  },
  created(){
    this.getData()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



