<template>
  <el-container>
    <el-header>
      <h3>板卡操作界面</h3>
    </el-header>
    <el-main  style="background: #fff">
     	<el-tabs v-model="activeName" @tab-click="handleClick">
		    <el-tab-pane v-for="(item,index) in cardList" :label="item.label" :name="item.numName" :key="index">
				<all-fun v-if="item.numName=='all'"></all-fun>
				<single-fun v-else :cardName='item'></single-fun>
			</el-tab-pane>
		</el-tabs>
    </el-main>
  </el-container>
</template>

<script>
	import allFun from './allFun'
	import singleFun from './singleFun'
export default {
  data () {
    return {
    	activeName:'all',
    	cardList:[
    		{numName:'all',label:'所有板卡管理'},
    		{numName:'one',label:'板卡1管理'},
    		{numName:'two',label:'板卡2管理'},
    	]
    }
  },
  components:{
  	allFun,singleFun
  },
  methods:{
  	  handleClick(tab, event) {
        // console.log(tab, event);
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



